import { useField } from "formik";
import React from "react";

const InputElement = ({ ...props }) => {
  const [field, meta] = useField(props);

  return (
    <>
      <div className=" w-80">
        <input
          {...field}
          className=" outline-none w-full h-10 px-4 border rounded-2xl border-gray-400 focus:border-orange-500"
          {...props}
        />
        {meta.error && meta.touched && (
          <p className=" text-red-600 font-mono font-light">{meta.error}</p>
        )}
      </div>
    </>
  );
};

export default InputElement;
