import React from "react";

const ButtonElement = (props) => {
  return (
    <>
      <div className="w-80">
        <button
          disabled={props.disabled}
          type={props.type}
          className="rounded-2xl  w-full h-10 bg-orange-500 text-white hover:opacity-85 transition ease-in-out delay-75"
        >
          {props.caption}
        </button>
      </div>
    </>
  );
};

export default ButtonElement;
