import * as yup from "yup";

export const tableGroupSchema = yup.object().shape({
  Groupname: yup
    .string()
    .min(3, "Too Short!")
    .max(6, "Too Long!")
    .required("Table Group Required"),
  inactive: yup.number().notOneOf([""]).required("Choose Inactive or not"),
});

export const tableSchema = yup.object().shape({
  Tablename: yup
    .string()
    .min(3, "Too Short!")
    .max(6, "Too Long!")
    .required("Table Name  Required"),
  GroupId: yup.string().notOneOf(["0"]).required("Choose a TableGroup"),
  capacity: yup.string().notOneOf(["0"]).required("Choose Table Capacity"),
  inactive: yup.number().notOneOf([""]).required("Choose Inactive or not"),
});

export const categorySchema = yup.object().shape({
  Category: yup
    .string()
    .min(3, "Too Short!")
    .max(15, "Too Long!")
    .required("Category Name Required"),
  inactive: yup.number().notOneOf([""]).required("Choose Inactive or not"),
});

export const itemGroupSchema = yup.object().shape({
  Group: yup
    .string()
    .min(3, "Too Short!")
    .max(15, "Too Long!")
    .required("Group Name  Required"),
  CategoryId: yup.string().notOneOf(["0"]).required("Choose a TableGroup"),
  inactive: yup.number().notOneOf([""]).required("Choose Inactive or not"),
});

export const itemSchema = yup.object().shape({
  item: yup
    .string()
    .min(3, "Too Short!")
    .max(20, "Too Long!")
    .required("Item Name Required"),
  gpid: yup.string().notOneOf(["0"]).required("Choose a ItemGroup"),
  price: yup
    .string()
    .max(12, "Invalid Price!")
    .matches(/^[0-9]*\.?[0-9]*$/, "Must be numbers")
    .required("Price is required"),
  inactive: yup.number().notOneOf([""]).required("Choose Inactive or not"),
  image: yup.mixed().required("Choose a image"),
});

export const itemupdateSchema = yup.object().shape({
  item: yup
    .string()
    .min(3, "Too Short!")
    .max(20, "Too Long!")
    .required("Item Name Required"),
  gpid: yup.string().notOneOf(["0"]).required("Choose a ItemGroup"),
  price: yup
    .string()
    .max(12, "Invalid Price!")
    .matches(/^[0-9]*\.?[0-9]*$/, "Must be numbers")
    .required("Price is required"),
  inactive: yup.number().notOneOf([""]).required("Choose Inactive or not"),
});

export const roleSchema = yup.object().shape({
  Role: yup
    .string()
    .min(3, "Too Short!")
    .max(15, "Too Long!")
    .required("Role Required"),
  inactive: yup.number().notOneOf([""]).required("Choose Inactive or not"),
});

export const userSchema = yup.object().shape({
  username: yup
    .string()
    .min(3, "Too Short!")
    .max(20, "Too Long!")
    .required("Username  Required"),
  roleid: yup.string().notOneOf(["0"]).required("Choose Role"),
  password: yup
    .string()
    .min(6, "At least 6 letters needed")
    .max(20, "Too Long!")
    .matches(
      /((?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[\W]).{6,20})/,
      "Password must include one digit,one lowercase letter, one special character"
    )
    .required("Password required"),
  inactive: yup.number().notOneOf([""]).required("Choose Inactive or not"),
});

export const loginSchema = yup.object().shape({
  username: yup
    .string()
    .min(3, "Too Short!")
    .max(20, "Too Long!")
    .required("Username Required"),
  password: yup
    .string()
    .min(6, "At least 6 letters needed")
    .max(20, "Too Long!")
    .matches(
      /((?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[\W]).{6,20})/,
      "Password must include one digit,one lowercase letter, one special character"
    )
    .required("Password required"),
});
