import React from "react";
import { LineWave } from "react-loader-spinner";

const LineSpinner = () => {
  return (
    <div className=" inset-0 z-50 fixed flex justify-center items-center backdrop-blur">
      <LineWave
        visible={true}
        height="100"
        width="100"
        color="#F97316"
        ariaLabel="line-wave-loading"
        wrapperStyle={{}}
        wrapperClass=""
        firstLineColor=""
        middleLineColor=""
        lastLineColor=""
      />
    </div>
  );
};

export default LineSpinner;
