import React, { useEffect, useState } from "react";
import InputElement from "../../formElements/InputElement";
import { Form, Formik } from "formik";
import ButtonElement from "../../formElements/ButtonElement";
import { itemGroupSchema } from "../../../util/Schema";
import { useSelector, useDispatch } from "react-redux";
import toast, { Toaster } from "react-hot-toast";
import ListIcon from "../../formElements/ListIcon";
import { useLocation, useNavigate } from "react-router-dom";
import SelectElement from "../../formElements/SelectElement";
import { category, getAllCategory } from "../../../redux/slices/CategorySlice";
import {
  createGroups,
  itemGroup,
  state,
  updateGroups,
  cleanup,
} from "../../../redux/slices/ItemGroupSlice";
import LineSpinner from "../../customLoader/LineSpinner";

const Group = () => {
  const [initial, setInitial] = useState({
    Group: "",
    CategoryId: "",
    inactive: "",
    id: "",
  });
  const [edit, setEdit] = useState(false);
  const { status, code } = useSelector(state);
  const Category = useSelector(category);
  const itemGroups = useSelector(itemGroup);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    if (Category.length === 0) {
      dispatch(getAllCategory());
    }
  }, []);
  useEffect(() => {
    if (code === 201 || code === 200) {
      toast.success(status);
    } else if (code === 500 || code === 404 || code === 403) {
      toast.error(status);
    }
    return () => {
      dispatch(cleanup());
    };
  }, [code, status]);

  useEffect(() => {
    if (location.state?.itemgroupId !== undefined && itemGroups.length > 0) {
      const identified = itemGroups.filter(
        (cat) => cat.id === location.state?.itemgroupId
      );
      const initialState = {
        Group: identified[0].Group,
        CategoryId: identified[0].CategoryId,
        inactive: identified[0].inactive,
        id: identified[0].id,
      };
      setInitial(initialState);
      setEdit(true);
    }

    return () => {
      location.state = null;
    };
  }, [location.state]);

  const navigationHandler = () => {
    navigate("../Group/List");
  };

  return (
    <>
      {status === "loading" && <LineSpinner />}
      <Toaster />
      <div className="px-20 py-8 overflow-auto flex  justify-center h-[calc(100vh-70px)]">
        <div className="  w-full md:w-1/3 px-4 ">
          <h1 className="text-center font-semibold text-3xl py-3">
            Item Group
          </h1>
          <div className="w-80 py-3">
            <div
              className="float-end  text-xl text-gray-700 font-semibold cursor-pointer hover:text-orange-500"
              onClick={navigationHandler}
            >
              <ListIcon />
            </div>
          </div>

          <Formik
            initialValues={initial}
            enableReinitialize={true}
            validationSchema={itemGroupSchema}
            onSubmit={(values, action) => {
              if (!edit) {
                dispatch(createGroups(values));
              } else {
                dispatch(updateGroups({ ...values }));
                setEdit(false);
              }

              action.setSubmitting(false);
              action.resetForm({
                values: {
                  Group: "",
                  CategoryId: "",
                  inactive: "",
                  id: "",
                },
              });
            }}
          >
            {(props) => {
              return (
                <Form>
                  <div className=" flex flex-col  justify-center items-center  text-center mt-5 gap-3">
                    <InputElement
                      type="text"
                      placeholder="Item Group Name"
                      name="Group"
                    />

                    <SelectElement name="CategoryId">
                      <option value="0">choose Category </option>
                      {Category.length > 0 &&
                        Category.map(
                          (cat, index) =>
                            cat.inactive === 0 && (
                              <option key={index} value={cat.id}>
                                {cat.Category}
                              </option>
                            )
                        )}
                    </SelectElement>
                    <SelectElement name="inactive">
                      <option value="">Inactive ? </option>
                      <option value="0">No</option>
                      <option value="1">Yes</option>
                    </SelectElement>

                    <ButtonElement
                      type="submit"
                      caption="SAVE"
                      disabled={props.isSubmitting}
                    />
                  </div>
                </Form>
              );
            }}
          </Formik>
        </div>
      </div>
    </>
  );
};

export default Group;
